import Vue from 'vue';
import VueRouter from 'vue-router';
//
//
import Signout from '@/components/Signout';
import Dashboard from '@/components/Dashboard';
import Login from '@/components/backstage/Login';

import Products from '@/components/backstage/Products';

import Coupons from '@/components/backstage/Coupons';
import Orders from '@/components/backstage/Orders';
import CustomerOrder from '@/components/backstage/CustomerOrders';

//
import Jindex from '@/components/fpages/Jindex';
import Productlist from '@/components/fpages/Productlist';

import Checkout from '@/components/fpages/Checkout';
import Checkorder from '@/components/fpages/Checkorder';
import Final from '@/components/fpages/Final';
import About from '@/components/fpages/About';
import Contact from '@/components/fpages/Contact';

Vue.use(VueRouter); //Router

export default new VueRouter({
  linkActiveClass: 'active',
  routes:[
    
    {
      name:'Jindex',//
      path:'/jindex',//
      component: Jindex,//
     },
     {
      name:'Checkout',//
      path:'/checkout',//
      component: Checkout,//
     },
     {
      name:'Checkorder',//
      path:'/checkorder',//
      component: Checkorder,//
     },
     {
      name:'Final',//
      path:'/final/:orderId',//
      component: Final,//
     },
     {
      name:'About',//
      path:'/about',//
      component: About,//
     },
     {
      name:'Contact',//
      path:'/contact',//
      component: Contact,//
     },
     {
      name:'Productlist',//
      path:'/productlist',//
      component: Productlist,//
      

     },
      //{
      // name:'',//
      // path:'/index',//
       //component: Home,//
       //meta: { requiresAuth: true },


      //},
      //{
       // name:'Login',
       // path:'*',//
        //component: Login,//
 
 
      // },
      {
        name:'Login',//
        path:'/login',//
        component: Login,//
 
 
       },
       {
        name:'Dashboard',//
        path:'/admin',//
        component: Dashboard,//
        children: [
          {
            path: 'products',
            name: 'Products',
            component: Products,
            meta: { requiresAuth: true },
          },
          {
            path: 'coupons',
            name: 'Coupons',
            component: Coupons,
            meta: { requiresAuth: true },
          },
          {
            path: 'orders',
            name: 'Orders',
            component: Orders,
            meta: { requiresAuth: true },
          },
        ],
 
 
       },
       {
        path: '/',
        name: 'Dashboard',
        component: Dashboard,
        children: [
          {
            path: 'customer_order',
            name: 'CustomerOrder',
            component: CustomerOrder,
          },
        ],
      },
      
  ],
});


